
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name: "collect",
props:['loginname'],
components: {
Card,
Group,
Cell
},
data: function() {
return {
userinfo: "",
collect_topics: ""
};
},
beforeRouteEnter: function(to, from, next) {
next(function(vm) {

vm.$http.get("/user/" + to.params.loginname).then(function(response) {
console.log(response.data);
vm.userinfo = response.data.data;
});

vm.$http
.get("/topic_collect/" + to.params.loginname)
.then(function(response) {
vm.collect_topics = response.data.data;
console.log(response.data.data)
});
});
},
beforeRouteUpdata: function(to, from, next) {
var app = this;
this.$http.get("/user/" + to.params.loginname).then(function(response) {
console.log(response.data.data);
app.userinfo = response.data.data;
});

app.$http
.get("/topic_collect/" + to.params.loginname)
.then(function(response) {
app.collect_topics = response.data.data;
});
next();
}
};



