export default {
    name:'center',
    // 获取登录名，即state数据
    // 方法一
    computed:{
        loginname:function(){
            return this.$store.state.loginname;
        }
    },
    created:function(){
        // 跳转到用户界面  使用编程式路由跳转
        // this.$router.push这个方法会向history添加一个记录，点击后退返回到上个界面
        // replace页面跳转有代替作用,建议使用this.$router.replace，不会向history添加记录
        // 点击会返回上上个页面，上一个页面不存在
        this.$router.replace('/user/'+this.loginname)
    }
}



