// vuex状态管理
// 使用方式：
// 1.引入vue和vuex
import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js';
import actions from './actions.js';

// 2. 全局注册
Vue.use(Vuex);

// 3. 创建Store对象
const store = new Vuex.Store({
    // 状态数据
    state:{
        topics:[
            {name:'all',desc:"全部",selected:true},
            {name:'good',desc:"精华",selected:false},
            {name:'share',desc:"分享",selected:false},
            {name:'ask',desc:"问答",selected:false},
            {name:'job',desc:"招聘",selected:false},
            {name:'dev',desc:"客户端测试",selected:false}
          ],
          // Actionsheet菜单
          unLoginedMoreMenus:{
            login:'登录'
          },
          // 用户已经登录时显示登出
          loginedMoreMenus:{
            create:'发表新话题',
            center:'个人中心',
            msg:'消息通知',
            logout:'注销'
          },
          // 登录状态
          isLogined:false,  //默认未登录
          // 登录用户名
          loginname:'',
          // accesstoken
          accesstoken:'',
          // 登录id
          author_id:'',
          // actionsheet显示与否变量
          isShowMoreMenus:false  // 默认不显示登录框
    },
    // 状态数据的计算属性
    getters:{
        moreMenus:function(state){
            if(state.isLogined){
              // 用户已经登录，返回已经登录的页面
              return state.loginedMoreMenus;
            }
            return state.unLoginedMoreMenus;
        },
        getTopicDescByTab:function(state){
          return function(tab){
            for(var i=0;i<state.topics.length;i++){
              if(state.topics[i].name == tab){
                return state.topics[i].desc;
              }
            }
          }
        }
    },
    // 修改同步数据的函数
    mutations,
    // 异步数据
    actions
})

// 4.对外输出
export default store;
// 5.在main.js中引入store


